.contacts-list__contact-details {
  display: grid;
  grid-template-columns: 76px 1fr;
  margin: 0;
  padding: 5px 0 10px;
  justify-content: space-between;
  font-style: normal;
}

@media (min-width: $tablet-width) {
  .contacts-list__contact-details {
    width: 50%;
  }
}

@media (min-width: $desktop-width) {
  .contacts-list__contact-details {
    width: 100%;
  }
}
